import axios from 'axios'
import AuthHeaders from './auth-header'

const BASE_USER_URL = 'http://localhost:8080/api/test'

class UserService {
  getAdminBoard () {
    return axios.get(BASE_USER_URL + '/admin', { headers: AuthHeaders() })
      .then(response => {
        console.log(response)
        return Promise.resolve(response)
      })
      .catch(error => {
        alert('Have problem with getting admin board', error.response.data, error.response.status)
      })
  }

  getUserBoard () {
    return axios.get(BASE_USER_URL + '/user', { headers: AuthHeaders() })
      .then(response => {
        console.log(response.data)
      })
  }

  getModeratorBoard () {
    return axios.get(BASE_USER_URL + '/mod', { headers: AuthHeaders() })
      .then(response => {
        console.log(response.data)
      })
  }
}

export default new UserService()



