<template>
  <div>
    <h1>This is Home Page , need Admin authorized</h1>
    <h3>Below is data of response or error message: </h3>
    <p>{{contentResponse}}</p>
  </div>
</template>

<script>
import UserService from '@/services/user-service'

export default {
  name: 'Home',
  data () {
    return {
      contentResponse: ''
    }
  },
  created () {
    UserService.getAdminBoard().then(
      res => {
        this.contentResponse = res.data
      }
    )
  }
}
</script>

<style>
p {
  font-size: 1.5rem;
  color: red;
}
</style>



